import "./style.css"
import { host } from '../../utils'

interface Options {
    containerId: string,
    clipId: string,
    clipIdx: number,
    videoTitle?: string,
    radioTitle?: string
    radioOptions?: string[]
    correctAns?: string[] | null,
    correctAnsReason?: string | null,
    hasSubmitBtn?: boolean,
    submitCorrectCallback?: Function | null,
}

export interface Answer {
    ans: string;
    events: any[];
    clipId: string;
}

export class VideoQuestion {

    private readonly _clipId: string
    get clipId() { return this._clipId }
    readonly clipIdx
    private readonly _host: string
    private readonly _correctAns: string[] | null
    private readonly _correctAnsReason: string | null
    private readonly _videoTitle: string
    private readonly _submitCorrectCallback: Function | null
    getTheCorrectAns = false
    hasPlayed = false

    // htmlElm
    private readonly _rootElm: HTMLElement
    private readonly _radioGroupElm: HTMLDivElement
    _videoElm: HTMLVideoElement
    private readonly _submitBtnElm: HTMLButtonElement

    get ans() { // define ans function
        const option = this._radioGroupElm.querySelector(`.option input:checked`) // get the option that is checked from the option class
        return option ? option.getAttribute('value') : null //get the value
    }

    /** @type {Array<{ type: String; value?:String; ts: Number }>} */
    _eventStack = []

    constructor(private readonly _options: Options) {
        const {
            containerId, clipId, clipIdx,
            videoTitle = '',
            radioTitle = 'Please categorize the clip based on what it mostly contains:', // default value
            radioOptions = ['Conceptual Animation', 'Raw Footage', 'Character Animation', 'Other'], // default value
            correctAns = null,
            correctAnsReason = null,
            hasSubmitBtn = false,
            submitCorrectCallback = null,
        } = _options

        this._host = `${host}/data/clip`
        this.clipIdx = clipIdx
        this._clipId = clipId
        this._rootElm = document.querySelector(`#${containerId}`)

        // add all the staffs
        this._rootElm.insertAdjacentHTML('beforeend', `
            <div class="video-wrapper">
                ${videoTitle ? `<div class="title" id="videoTitle">${videoTitle}</div>` : ''}
                <video class="video" controls src="${this._host}/${clipId}.mp4" type="video/mp4"></video>
            </div>
            <div class="radio">
                <label class="title">${radioTitle}</label>
                <div class="options"  id="radioDiv_${clipId}">
                    ${radioOptions.map((optLabel, idx) =>
            `<div class="option">
                            <input  type="radio" name="radioDiv_${clipId}" id="radio_${clipId}_opt_${idx}" value="${idx}"/>
                            <label for="radio_${clipId}_opt_${idx}">${optLabel}</label>
                        </div>`).join('')}
                    <div class="note"></div>
                </div>
            </div>
            ${hasSubmitBtn ? `<button class="button primary">Submit</button>` : ''}
        `)

        // video 
        // a flag to record whether video has been play
        this.hasPlayed = false
        this._videoTitle = videoTitle
        this._videoElm = this._rootElm.querySelector('.video')
        this._videoElm.addEventListener('play', () => {
            this.hasPlayed = true
        })
        this._mountEventHandlersForVideo(this._videoElm)

        // radio groups
        this._radioGroupElm = this._rootElm.querySelector('.radio')
        this._correctAns = correctAns
        this._correctAnsReason = correctAnsReason
        this._mountEventHandlersForRadio(this._radioGroupElm)

        // submit button
        if (hasSubmitBtn) {
            this._submitBtnElm = this._rootElm.querySelector('.button')
            this._submitBtnElm.addEventListener('click', (evt) => this.submitAns(evt, true))
            this._submitCorrectCallback = submitCorrectCallback
        }
    }

    hide() {
        this._rootElm.style.display = 'none'
    }

    show() {
        this._rootElm.style.display = 'initial'
    }

    submitAns(evt?: any, showAnswer = false) {
        if (!this.hasPlayed) {
            alert('You should play the video first!')
            return 0
        }

        if (this.getTheCorrectAns) {
            return 1
        }

        const ans = this.ans
        if (ans === null) {
            alert('You should select an answer!')
            return 0
        }

        // check the answer if correct answer exist
        if (this._correctAns !== null) {
            if (this._correctAns.includes(ans)) {
                showAnswer && this._setCurrentOptAsCorrect()
                this._videoElm.pause()
                // invoke the submitCorrect callback
                this._submitCorrectCallback && this._submitCorrectCallback()
                return 1
            } else {
                showAnswer && this._setCurrentOptAsWrong()
                return 0
            }
        } else { // else, return the select answer and event stacks
            this._eventStack.push({
                type: 'submit',
                ts: (new Date()).getTime()
            })
            return {
                ans,
                events: this._eventStack,
                clipId: this._clipId,
            }
        }
    }

    onPlay(handler) {
        if (typeof handler === 'function') {
            this._videoElm.addEventListener('play', handler)
        }
    }

    pauseVideo() {
        this._videoElm.pause()
    }

    _setCurrentOptAsWrong() {
        const optionElm = this._radioGroupElm.querySelector(`.option input:checked`)
        if (optionElm) {
            if (!optionElm.nextElementSibling.querySelector('span')) {
                optionElm.nextElementSibling
                    .insertAdjacentHTML('beforeend', '<span style="color:red; margin-left: 12px;">Wrong!</span>')
            }

            const noteElm = this._radioGroupElm.querySelector('.note') as HTMLDivElement
            noteElm.style.color = "red";
            noteElm.textContent = "==========>Please try again!<============";

        }
    }

    _setCurrentOptAsCorrect() {
        const optionElm = this._radioGroupElm.querySelector(`.option input:checked`)
        if (optionElm) {
            if (!optionElm.nextElementSibling.querySelector('span')) {
                optionElm.nextElementSibling
                    .insertAdjacentHTML('beforeend', '<span style="color:green; margin-left: 12px;">Correct!</span>')
            }

            const noteElm = this._radioGroupElm.querySelector('.note') as HTMLDivElement
            noteElm.style.color = "green"
            noteElm.textContent = this._correctAnsReason
            this.getTheCorrectAns = true

            // // set others to wrong
            // this._radioGroupElm.querySelectorAll('.option input:not(:checked)')
            //     .forEach(optionElm => {

            //         if (!optionElm.nextElementSibling.querySelector('span')) {
            //             optionElm.nextElementSibling
            //                 .insertAdjacentHTML('beforeend', '<span style="color:red; margin-left: 12px;"></span>')
            //         }
            //     })
        }
    }

    _mountEventHandlersForVideo(videoElm: HTMLVideoElement) {
        ['play', 'pause', 'stalled'].forEach(eventName => { // event stack arr
            videoElm.addEventListener(eventName, evt => {
                console.debug(`%con${eventName}`, 'background: #222; color: #bada55', evt)
                this._eventStack.push({ type: evt.type, ts: (new Date()).getTime() })
            })
        })
    }

    _mountEventHandlersForRadio(radioElm: HTMLElement) {
        radioElm.querySelectorAll('.option')
            .forEach(elm => elm.addEventListener('change', (evt) => {
                this._eventStack.push({ type: 'select', value: evt.target.value, ts: (new Date()).getTime() })
            }))
    } // close function
}