export const host = 'https://visualdictionary.ucsd.edu'
export const surveyLink = 'https://www.mturk.com/'

// @czt: I think you can skip this algorithm, it just shuffles an array
function shuffleArray<T>(oArray: T[]): T[] { // this function shuffles the names of the videos
    const array = [...oArray] //get all the individual properties one by one and transfer them to the new object.
    for (let i = array.length - 1; i > 0; i--) { // for the array of video names from the annotationMetadata.json file
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
    }
    return array
}

function initWorkerId() { //
    // if in the index path, store the workerId, assignmentId, and hitId
    if (location.pathname === '/') {
        const ids = location.search.substring(1).split('&').map(s => s.split('='))
        ids.forEach(d => { // store workerID in local storage/client browser
            if (d[0] === 'workerId') {
                localStorage.setItem(d[0], md5(d[1], 'vlang').substring(0, 12))
                localStorage.setItem('rawWorkerId', d[1])
            } else {
                localStorage.setItem(d[0], d[1])
            }
        })
    }

    if (!localStorage.getItem('workerId')) {
        let input
        do {
            input = prompt("Oops! Please input your workerId:");
        } while (input == null || input == "");

        localStorage.setItem('workerId', md5(input, 'vlang').substring(0, 12))
        localStorage.setItem('rawWorkerId', input)
    }
    document.body.querySelector('h1')
        .insertAdjacentHTML('beforeend', `<span style="color:steelblue;margin-left: 3em; font-size: 2rem;">Your workerId: ${localStorage.getItem('rawWorkerId')}</span>`)
}

function clearWorkerId() {
    localStorage.removeItem('workerId')
    localStorage.removeItem('rawWorkerId')
}

async function task_requestABlock() { //load worker id stored in browser and append to req url 
    const workerId = localStorage.getItem('workerId')
    const response = await fetch(`${host}/api/task?workerId=${workerId}`)
    const block = await response.json()

    return block as { errMsg: string, block?: BlockDocument, callbackLink?: string }
}

async function submitAns(payload: { blockId: string, nextIdx: number, answers: Answer[] }) {
    const workerId = localStorage.getItem('workerId')

    const response = await fetch(`${host}/api/submit`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            workerId,
            ...payload
        })
    })

    return await response.json() as { passATCheck: boolean }
}

async function task_generateCompletionCode() {
    const workerId = localStorage.getItem('workerId')
    const response = await fetch(`${host}/api/finish`, { // fetch API from api.js
        method: 'POST', // post the json content
        headers: {
            'Content-Type': 'application/json' // defo
        },
        body: JSON.stringify({
            workerId
        })
    })
    return await response.json() // return the completion code
}

// index
async function index_jumpToTaskIfAOldUser(callback?: Function) {
    const workerId = localStorage.getItem('workerId')
    const response = await fetch(`${host}/api/isOld?workerId=${workerId}`)

    const { isOld, pass } = await response.json()
    console.log('isOld', isOld, 'pass', pass)
    // if (pass === false) {
    //     callback && callback()
    // } else 
    if (isOld) {
        location.href = '/task.html'
    }
}

async function qualification_pass() {
    const workerId = localStorage.getItem('workerId')
    return await fetch(`${host}/api/passQualification?workerId=${workerId}`)
        .then(res => res.json())
}

